*{
  margin: 0;

}
.main{
background-color: #6c5b7b;
color: #ccf381;
background-image: url('https://burst.shopifycdn.com/photos/fog-on-dark-waters-edge.jpg?width=1200&format=pjpg&exif=1&iptc=1');
/* background-repeat: no-repeat; */
text-align: center;
background-size: 100%;
} 
img{
  margin-top:30px;
  width:20%;
}
h1{
  padding: 2%;
}
input[type=text], select {
  font-weight: bolder;
  width: 20%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
button {
  background-color: #453838;
  border: none;
  color: white;
  padding: 8px 8px;
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  margin-top:2px;
  cursor: pointer;
}
footer{
  text-align: center;
  color: white;
  font-weight: bold;
  text-decoration: underline;
}
